<template>
	<div id="register">
		<div class="content">
			<div class="header">
				<div class="logo">
					<img v-if="config.logo" :src="config.logo" alt=""/>
					<img v-else src="static/img/head.png" alt=""/>
				</div>
				<div class="name">{{config.title}}</div>
			</div>
			<div class="list-block">
				<van-cell-group>
					<van-field
							v-model="mobile"
							left-icon="phone1"
							type="number"
							placeholder="请输入手机号"
					/>
					<template v-if="verify == 1">
						<van-field
								v-model="captcha"
								left-icon="code"
								type="number"
								placeholder="请输入图形验证码"
						/>
						<van-field
								v-model="code"
								left-icon="email"
								type="number"
								placeholder="请输入6位短信验证码"
						/>
					</template>

					<van-field
						v-model="password"
						left-icon="lock"
						type="password"
						placeholder="请输入密码"
					/>
					<van-field
						v-model="repassword"
						left-icon="lock"
						type="password"
						placeholder="请重复输入密码"
					/>
				</van-cell-group>
				<template v-if="verify == 1">
					<div class="email-img" @click="onRefreshCaptcha">
						<img :src="captcha_img" alt="">
					</div>
					<div @click="onGetCode" class="list-password" :class="{disabled: code_disabled}" v-if="verify == 1">{{code_html}}<van-icon name='right' ></van-icon></div>
				</template>

				<div class="content-padded">
					<div @click="onSubmit" class="button-register">立即注册</div>
				</div>
			</div>
			<div class="text">
				<p>已有账号？<router-link :to="util.getUrl({path: '/pages/auth/login'})" class="text-a">立即登录</router-link></p>
			</div>
			<div class="tips">
				<van-checkbox v-model="loginAgreementCheck" icon-size="16px" checked-color="#ff2d4b">
					已认真阅读并同意
					<span class="c-danger" @click.stop="util.jsUrl('/pages/member/agreement', {type: 'agreement'})">用户服务协议</span>、<span class="c-danger" @click.stop="util.jsUrl('/pages/member/agreement', {type: 'privacy_policy'})">隐私政策</span>
				</van-checkbox>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<agreement :show="agreementPopup" title="服务协议/隐私政策" :content="config.agreement" @agreementHide="onAgreementHide"></agreement>
	</div>
</template>

<script>
	import agreement from '@/components/agreement'
	export default {
		data() {
			return {
				mobile: '',
				password: '',
				repassword: '',
				captcha: '',
				code: '',
				config: {},
				downcount: 60,
				captcha_img: '',
				code_html: "获取验证码",
				code_disabled: false,
				verify: 1,
				agreementPopup: false,
				showPreLoading: true,
				loginAgreementCheck: false,
			}
		},
		components: {
			agreement
		},
		methods: {
			onRefreshCaptcha() {
				this.util.request({
					url: 'wmall/auth/register/captcha',
				}).then((res) => {
					this.captcha_img = res.data.message.message.captcha;
				})
			},
			onSubmit() {
				if(!this.mobile){
					this.$toast('手机号不能为空');
					return false;
				}
				if(!this.util.isValidMobile(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if(this.verify == 1 && !this.code) {
					this.$toast('请输入短信验证码');
					return false;
				}
				if (!this.password) {
					this.$toast('密码不能为空');
					return false;
				} else {
					var length = this.password.length;
					if(length < 8 || length > 20) {
						this.$toast("请输入8-20位密码");
						return false;
					}
					var reg = /[0-9]+[a-zA-Z]+[0-9a-zA-Z]*|[a-zA-Z]+[0-9]+[0-9a-zA-Z]*/;
					if(!reg.test(this.password)) {
						this.$toast("密码必须由数字和字母组合");
						return false;
					}
				}
				if (!this.repassword) {
					this.$toast('请重复输入密码');
					return false;
				}
				if(this.password != this.repassword) {
					this.$toast('两次密码输入不一致');
					return false;
				}
				if(!this.loginAgreementCheck) {
					this.$toast('请勾选下方的 已认真阅读并同意 用户服务协议 和 隐私政策');
					return false;
				}
				this.util.request({
					url: 'wmall/auth/register',
					method: 'POST',
					data: {
						mobile: this.mobile,
						code: this.code,
						password: this.password,
						repassword: this.repassword
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message, '', 1000);
						return false;
					}
					result = result.message;
					this.util.setStorage('itoken', result.member.token);
					this.$router.replace(this.util.getUrl({path: '/pages/home/index'}));
				})
			},
			onGetCode() {
				if(this.verify != 1) {
					return false;
				}
				if(this.downcount != 60) {
					return false;
				}
				if(!this.mobile) {
					this.$toast('请输入手机号');
					return false;
				}
				if(!this.util.isValidMobile(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if(!this.captcha) {
					this.$toast('请输入图形验证码');
					return false;
				}

				this.util.request({
					url: 'system/common/code',
					method: 'POST',
					data: {
						mobile: this.mobile,
						captcha: this.captcha
					}
				}).then((res) => {
					let result = res.data.message
					if(result.errno) {
						this.$toast(result.message);
						return;
					} else {
						var timer = setInterval(() => {
							this.downcount--;
							if(this.downcount <= 0){
								clearInterval(timer);
								this.code_html = "获取验证码";
								this.downcount = 60;
							} else {
								this.code_disabled = true;
								this.code_html = this.downcount + "秒后重新获取";
							}
						}, 1000);
						this.$toast('验证码发送成功, 请注意查收');

					}
				})
			},
			onLoad() {
				this.util.request({
					url: 'wmall/auth/register',
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message, '', 1000);
						return false;
					}
					this.config = result.message.config_mall;
					this.captcha_img = result.message.captcha;
					this.verify = result.message.verify;
				})
			},
			onAgreementHide() {
				this.agreementPopup = !this.agreementPopup;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#register .content{
		position: fixed;
		top: 0px;
		right: 0;
		left: 0;
		bottom: 0px;
		width: 100%;
		background: #fff;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#register .content::-webkit-scrollbar {
		display: none;
	}
	#register .content .header{
		height: 160px;
		background: url(../../../static/img/bg-mypage.png) no-repeat 0 0;
		background-size: 100% 100%;
		overflow: hidden;
	}
	#register .content .header .logo{
		position: relative;
		height: 88px;
		width: 88px;
		border-radius: 100%;
		margin: 20px auto 0;
	}
	#register .content .header .logo:before{
		position: absolute;
		content: "";
		height: 94px;
		width: 94px;
		border: 1px solid #fff;
		border-radius: 94px;
		top: -4px;
		left: -4px;
	}
	#register .content .header .logo img{
		height: 88px;
		width: 88px;
		border-radius: 88px;
		display: block;
		border: none;
		max-width: 100%;
	}
	#register .content .header .name{
		height: 50px;
		color: #fff;
		text-align: center;
		font-size: 20px;
		line-height: 50px;
	}
	#register .content .list-block{
		position: relative;
		overflow: hidden;
	}
	#register .content .list-block .van-cell .van-icon{
		font-size: 16px;
		margin-right: 12px;
	}
	#register .content .list-block .van-cell .van-cell__value .van-field__body{
		font-size: 17px;
	}
	#register .content .list-block .email-img{
		height: 32px;
		width: 90px;
		position: absolute;
		top: 49px;
		right: 15px;
	}
	#register .content .list-block .email-img img{
		border: none;
		width: 100%;
		height: 100%;
	}
	#register .content .list-block .list-password{
		z-index: 10000;
		position: absolute;
		top: 98px;
		right: 15px;
		font-size: 14px;
		color: #888;
		line-height: 24px;
	}
	#register .content .list-block .content-padded{
		margin: 20px 20px 10px 20px;
	}
	#register .content .list-block .content-padded .button-register{
		display: block;
		border-radius: 25px;
		font-size: 17px;
		color: #fff;
		background-color: #f6383a;
		height: 40px;
		line-height: 40px;
		width: 100%;
		text-align: center;
		text-decoration: none;
		text-overflow: ellipsis;
		white-space: nowrap;
		cursor: pointer;
	}
	#register .content .tips{
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 0 15px 30px;
		text-align: center;
		line-height: 1.5;
		font-size: 12px;
		color: #7a7a7a;
	}
	#register .content .text{
		padding: 0 32px;
		color: #888;
		font-size: 16px;
		text-align: center;
		line-height: 24px;
	}
	#register .content .text .text-a{
		color: #0894ec;
	}
	#register .van-nav-bar__title{
		color: #fff;
	}
	#register .tips .van-checkbox{
		justify-content: center;
	}
	#register .tips .van-checkbox .van-checkbox__label{
		margin-left: 5px;
	}
</style>



